.main-section{
    background-color: #25252B;
    color: #fff;
    height: 100vh;
    }
    .img-container{
        height: 100vh;
        overflow: hidden;
    }
    .img-container img{
        height: 100%;
        position: relative;
        left: -74px;
          transition: all 0.5s;
    }
    .img-box{
        -webkit-flex: 1;
        -ms-flex: 1;
        flex: 1;
        cursor: pointer;
        -webkit-transition: all 500ms ease;
        -moz-transition: all 500ms ease;
        -ms-transition: all 500ms ease;
        -o-transition: all 500ms ease;
        transition: all 500ms ease;
        position: relative;
        color: #fff;
    }
    .img-box:hover{
       flex-grow: 3;
       color: #fff;
    }
    .frist{
        background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/1.jpg");
        background-attachment: fixed;
    }
    .second{
        background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/2.jpg");
        background-attachment: fixed;
    }
    .last{
        background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/3.jpg");
        background-attachment: fixed;
    }
    .flex-contain{
      position: absolute;
        height: 100vh;
        width: 100%;
        display: -webkit-flex;
        display: flex;
        overflow: hidden;
    }
    .img-text{
        position: absolute;
        bottom: 10px;
    }
    .img-text h3{
        font-size: 30px;
        word-wrap: break-word;
    }
    .main-heading-text{
        position: relative;
        top: 45%;
        right: -42%;
        z-index: 1;
        width: 100%;
    }
    .main-heading-text h1{
        font-size: 60px;
    }

    @media (max-width: 767.98px) { 
        .main-section {
            height: 100%;
        }
        .img-container {
            height: 100%;
        }
        .main-heading-text {
            top: 2%;
            right: 0%;           
        }
        .main-heading-text h1 {
            font-size: 40px;
        }
        .insta-boxs{
            flex-direction: column;
            margin: 11px;
        }
        .img-box {
            flex: auto; 
            width: 100%;
            height: 250px;
            border-radius: 20px;
            margin: 10px 0;
        }
     }
